import { useState } from "react";
import {
  AppBar,
  Box,
  Drawer,
  IconButton,
  styled,
  Toolbar,
  useMediaQuery,
  Typography,
  Button,
  Divider,
  Grid,
  ListItem,
} from "@mui/material";
import { Selector as SelectorIcon } from "icons/Selector";

import MenuIcon from "@mui/icons-material/Menu";
import CloseIcon from "@mui/icons-material/CloseOutlined";
import { Telegram, Twitter, Facebook, WhatsApp } from "@mui/icons-material";

const items = [
  { key: "history", icon: <></>, href: "#history", title: "History" },
  { key: "tokens", icon: <></>, href: "#tokens", title: "Tokens" },
  { key: "countdown", icon: <></>, href: "#countdown", title: "Countdown" },
  { key: "share", icon: <></>, href: "#share", title: "Share" },
];

export default function Navigation() {
  const mdUp = useMediaQuery((theme: any) => theme.breakpoints.up("md"), {
    defaultMatches: true,
    noSsr: false,
  });

  const [isOpen, setOpen] = useState(false);
  const handleClose = () => setOpen(false);
  const handleOpen = () => setOpen(true);

  const content = (
    <>
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          height: "100%",
        }}
      >
        <CloseButton handleClose={handleClose} />
        <NetworkSelect />
        <NavDivider />
        <Box sx={{ flexGrow: 1 }}>
          {items.map((item) => (
            <NavItem key={item.title} icon={item.icon} href={item.href} title={item.title} />
          ))}
        </Box>

        <Grid p={1} container justifyContent={"space-around"} alignItems={"flex-start"}>
          <IconButton>
            <WhatsApp />
          </IconButton>
          <IconButton>
            <Telegram />
          </IconButton>
          <IconButton>
            <Twitter />
          </IconButton>
          <IconButton>
            <Facebook />
          </IconButton>
        </Grid>
      </Box>
    </>
  );

  if (mdUp) {
    return (
      <Navbar>
        <Toolbar>
          <Title />
          <Box sx={{ flexGrow: 1 }} />
        </Toolbar>
      </Navbar>
    );
  }

  return (
    <>
      <Navbar>
        <Toolbar>
          <Title />
          <Box sx={{ flexGrow: 1 }} />
          <IconButton onClick={handleOpen}>
            <MenuIcon />
          </IconButton>
        </Toolbar>
      </Navbar>
      <Drawer
        open={isOpen}
        onClose={handleClose}
        anchor="left"
        PaperProps={{
          sx: {
            backgroundColor: "neutral.900",
            color: "#FFFFFF",
            width: 280,
          },
        }}
        sx={{ zIndex: (theme) => theme.zIndex.appBar + 100 }}
        variant="temporary"
      >
        {content}
      </Drawer>
    </>
  );
}

const Navbar = styled(AppBar)(({ theme }) => ({
  backgroundColor: theme.palette.background.paper,
  boxShadow: "none",
}));

const Title = () => (
  <Typography variant="h4" fontWeight={"bold"}>
    METAVERSE
  </Typography>
);

const NavDivider = () => (
  <Divider
    sx={{
      borderColor: "#2D3748",
      my: 3,
    }}
  />
);

function CloseButton(props: { handleClose: () => void }) {
  return (
    <Grid container justifyContent={"flex-end"} alignItems={"center"} p={1}>
      <IconButton onClick={props.handleClose}>
        <CloseIcon />
      </IconButton>
    </Grid>
  );
}

function NetworkSelect() {
  return (
    <Box sx={{ px: 2 }}>
      <Box
        sx={{
          alignItems: "center",
          backgroundColor: "rgba(255, 255, 255, 0.04)",
          cursor: "pointer",
          display: "flex",
          justifyContent: "space-between",
          px: 3,
          py: "11px",
          borderRadius: 1,
        }}
      >
        <div>
          <Typography color="inherit" variant="subtitle1">
            Polygon
          </Typography>
          <Typography color="neutral.400" variant="body2">
            Change network
          </Typography>
        </div>
        <SelectorIcon
          sx={{
            color: "neutral.500",
            width: 14,
            height: 14,
          }}
        />
      </Box>
    </Box>
  );
}

export const NavItem = (props: any) => {
  const { href, icon, title, ...others } = props;
  // const router = useRouter();
  // const active = href ? router.pathname === href : false;
  const active = false;

  return (
    <ListItem
      disableGutters
      sx={{
        display: "flex",
        mb: 0.5,
        py: 0,
        px: 2,
      }}
      {...others}
    >
      {/* <NextLink href={href} passHref> */}
      <Button
        component="a"
        startIcon={icon}
        disableRipple
        fullWidth
        sx={{
          backgroundColor: "rgba(255,255,255, 0.08)",
          borderRadius: 1,
          color: "secondary.main",
          fontWeight: "fontWeightBold",
          justifyContent: "flex-start",
          px: 3,
          textAlign: "left",
          textTransform: "none",
          "& .MuiButton-startIcon": {
            color: active ? "secondary.main" : "neutral.400",
          },
          "&:hover": {
            backgroundColor: "rgba(255,255,255, 0.08)",
          },
        }}
      >
        <Box sx={{ flexGrow: 1 }}>{title}</Box>
      </Button>
      {/* </NextLink> */}
    </ListItem>
  );
};
