import { useMediaQuery } from "@mui/material";
import React, { useRef, useCallback, useState } from "react";
import { Carousel } from "react-responsive-carousel";

import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader

const wvideo1 = require("assets/display/1.mp4");
const wvideo2 = require("assets/display/2.mp4");
const wvideo3 = require("assets/display/3.mp4");
const wvideo4 = require("assets/display/4.mp4");

const mvideo1 = require("assets/mobile/1.mp4");
const mvideo2 = require("assets/mobile/2.mp4");
const mvideo3 = require("assets/mobile/3.mp4");
const mvideo4 = require("assets/mobile/4.mp4");

interface VideoProps {
  src: string;
}

const Video = ({ src }: VideoProps) => {
  const videoRef = useRef<any>(null);
  const [playing, setPlaying] = useState(false);

  const handlePlayPause = useCallback(() => {
    videoRef.current.play();
    videoRef.current.onpause = () => {
      setPlaying(false);
    };
    setPlaying(true);
  }, [videoRef]);

  return (
    <div className="video-container">
      <video ref={videoRef}>
        <source src={src} type="video/mp4" />
      </video>
      <div className="message">
        {!playing && <div onClick={handlePlayPause} className="play" />}
      </div>
    </div>
  );
};

export default function VideoCarousel() {
  const mdUp = useMediaQuery((theme: any) => theme.breakpoints.up("md"), {
    defaultMatches: true,
    noSsr: false,
  });

  const carouselRef = useRef<any>(null);

  const videos = [
    { wide: wvideo1, mobile: mvideo1 },
    { wide: wvideo2, mobile: mvideo2 },
    { wide: wvideo3, mobile: mvideo3 },
    { wide: wvideo4, mobile: mvideo4 },
  ];

  return (
    <Carousel
      autoPlay={false}
      showThumbs={false}
      showStatus={false}
      showArrows={false}
      useKeyboardArrows
      transitionTime={0}
      dynamicHeight
      ref={carouselRef}
    >
      {videos.map((v, i) => (
        <Video key={i} src={mdUp ? v.wide : v.mobile} />
      ))}
    </Carousel>
  );
}
